import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from '../core/auth.service';
import { ServiceProviderService } from '../core/service-provider.service';

@Component({
  selector: 'app-pet-trainers',
  templateUrl: './pet-trainers.page.html',
  styleUrls: ['./pet-trainers.page.scss'],
})
export class PetTrainersPage implements OnInit {
  constructor(
    private activatedRoute: ActivatedRoute,
    private service: ServiceProviderService
  ) {
    console.log('working');
  }

  ngOnInit() {}
  ionViewWillEnter() {
    this.getData();
  }

  getData() {
    console.log('working');
    let category = this.activatedRoute.snapshot.queryParams.category;
    this.service.getServiceProvider(category).subscribe((res) => {
      console.log(res);
    });
  }
}
